<template>
  <div class="supply-list-panel">
    <supply-list-option></supply-list-option>
    <a-table :columns="columns" :dataSource="data" :showHeader="false" size="small">
      <div slot="supply-title" slot-scope="text, record">
        <p style="line-height: 1;margin:0 0 0.2em;">{{text}}</p>
        <p style="line-height: 1;margin:0;font-size:12px;color: #999;">{{record && record.desc}}</p>
      </div>
      <template slot="supply-price" slot-scope="text">${{text}}</template>
      <template slot="supply-num" slot-scope="text">{{text}}接单</template>
      <template slot="supply-time" slot-scope="text">
        <a-icon type="clock-circle"/>
        {{text}}
      </template>
      <template slot="supply-recommend" slot-scope="text, record, index">
        <a-icon v-if="index < 4" type="star" style="color: #ff0000;font-size:20px;"/>
      </template>
    </a-table>
  </div>
</template>

<script>
import SupplyListOption from './ListOption'
const columns = [{
  dataIndex: 'title',
  scopedSlots: { customRender: 'supply-title' }
}, {
  dataIndex: 'price',
  width: '10%',
  scopedSlots: { customRender: 'supply-price' }
}, {
  dataIndex: 'num',
  width: '10%',
  scopedSlots: { customRender: 'supply-num' }
}, {
  dataIndex: 'time',
  width: '20%',
  scopedSlots: { customRender: 'supply-time' }
}, {
  dataIndex: 'recommend',
  width: '10%',
  scopedSlots: { customRender: 'supply-recommend' }
}]

const data = []
for (let i = 0; i < 100; i++) {
  data.push({
    key: i.toString(),
    title: `测试标题 ${i}`,
    desc: `测试描述.... ${i}`,
    price: 10,
    num: 10,
    time: `剩余时间${i}`
  })
}
export default {
  components: { SupplyListOption },
  data () {
    this.cacheData = data.map(item => ({ ...item }))
    return {
      data,
      columns
    }
  },
  methods: {
    handleChange (value, key, column) {
      const newData = [...this.data]
      const target = newData.filter(item => key === item.key)[0]
      if (target) {
        target[column] = value
        this.data = newData
      }
    },
    edit (key) {
      const newData = [...this.data]
      const target = newData.filter(item => key === item.key)[0]
      if (target) {
        target.editable = true
        this.data = newData
      }
    },
    save (key) {
      const newData = [...this.data]
      const target = newData.filter(item => key === item.key)[0]
      if (target) {
        delete target.editable
        this.data = newData
        this.cacheData = newData.map(item => ({ ...item }))
      }
    },
    cancel (key) {
      const newData = [...this.data]
      const target = newData.filter(item => key === item.key)[0]
      if (target) {
        Object.assign(target, this.cacheData.filter(item => key === item.key)[0])
        delete target.editable
        this.data = newData
      }
    }
  }
}
</script>
